import axios from "axios";
import { React, useState, useEffect } from "react";
import ProductItem from "./productItem";
import AddProduct from "./AddProduct";
import { Input, Space } from "antd";
import { AudioOutlined } from "@ant-design/icons";
import './../index.css'
const ProductList = () => {
  const [post, setPost] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const { Search } = Input;

  const API_KEY = "28300e83c5af41f0823add53710cac76";

  const api = axios.create({
    //baseURL: `http://localhost:4000/api/image-data`,
    baseURL: `https://proclick2022.azurewebsites.net/api/image-data`,
  });

  const suffix = (
    <AudioOutlined
      style={{
        fontSize: 16,
        color: "#1890ff",
      }}
    />
  );

  const onSearch = (value) => console.log(value);
  //const BASE_URL = "";

  useEffect(() => {
    loadProducts();
  }, []);

  const loadProducts = (title) => {
    api
      .get("/")
      .then((response) => {
        console.log(response.data);
        var data = Array.from(response.data);
        setPost(data);
      })
      .catch((err) => console.log("_____" + err));
  };

  const searchProducts = () => {
    api
      .get(`/${searchTerm}`)
      .then((response) => {
        console.log(response.data);
        setPost(response.data);
      })
      .catch((err) => console.log(err));
  };

  // const searchHandler = (e) => {
  //   const result = post.filter(
  //     (obj) => (obj) =>
  //       typeof obj.title === "string" && obj.title.includes(searchTerm)
  //   );
  //   // const result = post.filter(obj=> obj.title === searchTerm)

  //   setPost(result);
  // };

  if (!post) return "No post";
  return (
    <>
      <br />
      <div className="searchBar">
        <input
          value={searchTerm}
          onChange={(e) => {
            setSearchTerm(e.target.value);
          }}
        />

        <button onClick={() => searchProducts()}>Search</button>
      </div>

      <br />

      <div className="product-list">
        {
        post.length > 0 ? (
          post?.map((data, index) => (
            <div>
              <ProductItem
              className="list"
                title={data.title}
                description={data.description}
                imageUrl={data.image_url}
                key={index}
              />
            </div>
          ))
        ) : (
          <h3> No Product Exist</h3>
        )}
      </div>
    </>
  );
};

export default ProductList;
